import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides, LoadingController, Loading, Platform, normalizeURL, AlertController } from 'ionic-angular';
import { RoomissueServiceProvider } from '../../providers/roomissue-service/roomissue-service';
import { ToolsProvider } from '../../providers/tools/tools';
import { Restaurant } from '../../model/restaurant-model';
import { NotificationProvider } from '../../providers/notification/notification';

import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Camera } from '@ionic-native/camera';

//Xevi
import { ToastController } from 'ionic-angular';

/**
 * Generated class for the RestaurantsPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-roomissuedetails',
  templateUrl: 'room-issue-details.html',
})
export class RoomissueDetailsPage {

  issueImage: any = 'assets/imgs/background_photo.jpg';

  formIndex: number;
  documentType: any;
  isPhotoAdded: boolean = false;
  photoTemplateHtml: string = '';
  devicePlatForm: string = "android";
  issueDetailsForm: FormGroup;

  constructor(private transfer: FileTransfer, private file: File, public navCtrl: NavController,
    public navParams: NavParams, private roomissueProvider: RoomissueServiceProvider,
    public toolsProvider: ToolsProvider, public loadingCtrl: LoadingController,
    private notificationProvider: NotificationProvider, private platForm: Platform,
    public formBuilder: FormBuilder,
    private camera: Camera,
    public alertCtrl: AlertController,
    private toastCtrl: ToastController) {

    this.issueDetailsForm = formBuilder.group({
      'comments': new FormControl('')
    });


  }

  ionViewDidLoad() {

  }

  ionViewWillLeave() {
    //save active slider index
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  public fnGetPicture() {
    this.fnAttachFile(this.camera.PictureSourceType.CAMERA);
    this.isPhotoAdded = true;
  }

  public fnSubmit() {
    if (this.issueDetailsForm.valid){
      //Enviem la peticiÃ³ i esperem resposta....
      let alertnew = this.alertCtrl.create({
        title: 'ALERT',
        message: 'ALERT OK',
        cssClass: 'cls_alrt',
        buttons: [
          {
            text: 'OK',
            role: 'cancel'
          }]
      });
      alertnew.present();
      setTimeout(() => { alertnew.dismiss(); }, 5000);
    }
  }

  public fnAttachFile(sourceType) {
    var imageoptions: any = {
      quality: 30,
      targetWidth: 500,
      targetHeight: 500,
      allowEdit: false,
      sourceType: sourceType,
      mediaType: this.camera.MediaType.PICTURE,
      destinationType: this.camera.DestinationType.DATA_URL
    }
    return this.camera.getPicture(imageoptions)
      .then((imageData) => {
        console.log('fileUri: ' + imageData);
        this.issueImage = "data:image/jpeg;base64," + imageData;
        //this.roomissueProvider.objRoomissue.pictureFront[this.formIndex] = imageData;

      }).catch((error) => {
        console.log("Camera error" + error);
      })
  }

  //Xevi
  private fndebug(message) {
    let toast = this.toastCtrl.create({
      message: message,
      duration: 3000,
      position: 'top'
    });

    toast.onDidDismiss(() => {
    });

    toast.present();
  }


}



